@media(max-width: $ipad ){
  .sort {
    display: block;
  &__categories {
  }
  &__type {
    margin-top: 20px;
    display: flex;
    justify-content: flex-end;
    margin-right: 18px;
  }
}
.categories{
  &__item {
    padding: 13px 25px;
  }
}
}

@media(max-width: $ipad-mini ){
.sort {
  &__categories {
  }
}

.categories {
  justify-content: space-around;
  li {
    padding: 13px 13px;
    font-size: 14px;
  }
}
}

@media(max-width: $ipad-ultra-mini ){
  .sort {  
  &__categories {
  }
}
.categories {
  margin: 0 auto;
  max-width: 330px;
  overflow: scroll;
  justify-content: flex-start;
  padding-bottom: 25px;
  
  &__item {
  }
}
}
