.cart-page {
  max-width: 820px;
  margin: 0 auto;
  // background-color: aqua;
  min-height: 70vh;
  display: flex;
  flex-direction: column;
		&__top {
      margin: 90px 0 70px 0;
		}

		&__list {
      margin-bottom: 40px;
      flex: 1 0 100%;
		}

		&__pizza-cart {
      margin-bottom: 60px;
		}

		&__total {
      margin-top: 40px;
      
		}

		&__bottom {
		}
}
.top {
  display: flex;
  justify-content: space-between;
  align-items: center;

		&__basket {
      display: flex;
      align-items: center;
		}

		&__basket-img {
      width: 30px;
      height: 30px;
      margin-right: 10px;
		}

		&__trash {
      display: flex;
      align-items: center;
		}

		&__trash-img {
      width: 30px;
      height: 30px;
      margin-right: 10px;
		}
}
.pizza-cart {
  // background-color: $mainBackground;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px -25px 20px -20px rgba(0, 0, 0, 0.45), 0px 25px 20px -20px rgba(0, 0, 0, 0.45);

		&__img {
      flex:0 0 120px;
      height: 120px;
		}

		&__text {
      flex: 1 0 150px;
      text-align: center;
      // background: red;
      p{
        font-weight: 300;
        font-size: 18px;
      }
		}

		&__title {
      font-weight: 700;
      font-size: 22px;
		}

		&__regulator {
      display: flex;
      align-items: center;
      h3{
        margin: 0 12px;
        font-size: 20px;
        font-weight: 600;
        color: red
      }
		}

		&__minus {
      width: 32px;
      height: 32px;
      background:inherit;
		}
    &__container{
      display: flex;

    }
		&__price {
      display: flex;
      align-items: center;
      margin: 0 15px;
      h3{
        font-weight: 700;
        font-size: 22px;
      }
		}

		&__price-img {
      height: 17px;
      width: 17px;
    
		}

		&__clean-img {
      flex: 0 0 32px;
      height: 32px;
		}
}
.total-cart {
  display: flex;
  justify-content: space-between;
		&__count {
      p{
        font-weight: 400;
        font-size: 22px;
      }
      span{
        font-weight: 600;
      }
		}

		&__price {
      p{
        font-weight: 400;
        font-size: 22px;
      }
      span{
        font-weight: 600;
      }
		}
}
.bottom {
  margin: 0 50px 0 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;


  &__button {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 0 0 215px;
    border: 1px solid black;
    border-radius: 30px;
    height: 45px;
    font-weight: 700;
    font-size: 16px;

  }

  &__img {
    margin-right: 10px;
  }
}
